.curordrs{
  margin-top: 6vw;
  padding-bottom: 10vw;

  &__tab{
    margin-top: 6vw;
  }
  &__item{
    position: relative;
    display: block;
    margin-bottom: 6vw;
    padding: 6vw;
    border-radius: 6vw;
    background: rgb(237, 243, 245);
  }
  &__term{
    margin: 6vw 0 0 0;
    display: flex;
    flex-wrap: wrap;
    // justify-content: flex-end;
    gap: 2vw;

    &&_v2{
      li{
        background: var(--color-main);
        color: #fff;

        &>i svg{
          fill: #fff;
        }
      }
    }

    li{
      display: flex;
      align-items: center;
      padding: 0.4em 1.45em 0.45em;
      border-radius: 2vw;
      background: #fff;
      box-shadow: 14px 14px 40px 0px rgba(56, 106, 94, 0.16);
      font-size: 0.8em;

      &[data-status="complete"]{
        background: var(--color-main);
        color: #fff;
      }
      &[data-status="process"]{
        background: #FF9900;
        color: #fff;
      }
      &[data-status] svg{
        fill: currentColor;
      }

      &>i{
        display: block;
        width: 6vw;
        flex: 0 0 6vw;
        margin-right: 3vw;

        svg{
          fill: rgb(139, 139, 139);
        }
      }
    }
  }
  &__title{
    display: inline-block;
    font-weight: 700;
    font-size: 1.2em;
    
    &:hover > i svg{
      fill: var(--color-2);
    }

    &>i{
      display: inline-block;
      position: relative;
      top: 0.1em;
      width: 30/28em;
      margin-left: 0.3em;

      svg{
        fill: var(--color-main);
        transition: 0.3s fill;
      }
    }
  }
  &__body{
    margin-top: 6vw;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__col-1{
    width: 100%;
    margin-bottom: 4vw;
    display: flex;
    flex-wrap: wrap;
    gap: 6vw 12vw;
  }
  &__col-1-1{
    // margin-bottom: 4vw;
  }
  &__txt{
    display: block;
    margin-bottom: 0.2em;
    font-weight: 600;
  }
  &__label{
    display: block;
    color: rgb(156, 165, 172);
    font-size: 14/16em;
  }
  &__price{
    display: block;
    font-weight: 700;
    font-size: 1.2em;
    color: var(--color-main);
  }
  &__goto{
    order: 5;
    margin-left: auto;
  }
}


@media (min-width: @w-lg) {
	.curordrs{
    margin-top: 2.1vw;
    padding-bottom: 5vw;

    &__tabs{
      width: 87000/1920vw;
    }
    &__tab{
      margin-top: 2vw;
      width: 87000/1920vw;
    }
    &__item{
      margin-bottom: .93vw;
      border-radius: 4400/1920vw;
      padding: 2.05vw 1.8vw 1.56vw 2.3vw;

      &&_v2{
        padding-bottom: 2.08vw;

        .curordrs__term{
          top: 1.65vw;
          right: 1.55vw;
        }
        .curordrs__col-1{
          margin-left: 0;
          width: 100%;
          gap: 4.1vw;
        }
        .curordrs__body{
          margin-top: 2vw;
        }
      }
    }
    &__term{
      position: absolute;
      top: 1.35vw;
      right: 1.35vw;
      margin: 0;
      gap: 1.15vw;
      
      li{
        font-size: 0.73vw;
        border-radius: .52vw;
        padding-bottom: 0.5em;

        &>i{
          width: .95vw;
          flex-basis: .95vw;
          margin-right: 0.4vw;
        }
      }
    }
    &__title{
      font-size: 1.45vw;
    }
    &__body{
      margin-top: 1.2vw;
    }
    &__goto{
      width: 2.08vw;

      .link-next{
        width: 2.08vw;
      }
    }
    &__col-1{
      width: 26vw;
      gap: 2.4vw;
      order: 2;
      margin:  0 0 0 5.7vw;
    }
    &__col-1-1{
      // margin: 0;
    }
    &__price{
      font-size: 1.45vw;
    }
    &__label{
      font-size: 0.73vw;
    }
    &__txt{
      font-size: 0.83vw;
    }
  }
}
