.compra{
  margin-top: 10vw;

  &__aside{
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    // min-height: 91500/1920vw;
    border-radius: 4vw;

    &-title{
      display: block;
      font-weight: 700;
      line-height: 1.5;
      font-size: 4.5vw;
    }
    &-clear{
      margin-top: 6vw;
      margin-bottom: auto;
    }
    &&_desktop{
      display: none;
    }
  }
  &__list{

    li{
      margin-bottom: 4vw;

      // &:last-child{
      //   margin-bottom: 0;
      // }

      strong{
        color: var(--color-main);
      }
    }

    &&_titles{
      padding: 0;
      color: rgb(139, 139, 139);
    }
  }
  &__scrollbar-1{
    margin-bottom: 6vw;
  }
  &__scrollbar-2{
    margin-top: 6vw;
  }
  &__slider{
    &-wrap{

      .swiper-slide{
        width: 64vw;
        padding: 0 2vw;
        box-sizing: content-box;
        border-right: 1px solid rgb(204, 221, 226);
        height: auto;

        &:last-child{
          border-right-width: 0;
        }
        &:first-child{
          width: 50vw;
        }
      }
    }
  }
  &__slide{
    padding: 4vw;
    border-radius: 6vw;
    background: rgb(237, 243, 245);
  }
  &__pic{
    position: relative;
    display: block;
    margin-bottom: 6vw;

    img{
      aspect-ratio: 246/163;
      border-radius: 4vw;
      object-fit: cover;
    }
  }
  &__remove{
    position: absolute;
    top: 4vw;
    right: 4vw;
  }
  &__link{
    display: inline-block;
    margin-bottom: 6vw;
    font-weight: 700;
    height: 2*1.3em;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;  
    overflow: hidden;
  }
  &__adds{
    text-align: center;
  }
  &__list{
    margin-top: 10vw;
    padding-left: 6vw;
  }
}


@media (min-width: @w-lg) {
	.compra{
    margin-top: 2.1vw;

    &__wrap{
      display: flex;
    }
    &__aside{
      position: relative;
      display: flex;
      flex-direction: column;
      // min-height: 91500/1920vw;
      width: 29400/1920vw;
      border-radius: 2.08vw;
      background: rgb(253, 253, 255);
      padding: 2.35vw 1.5vw 3.05vw 1.5vw;

      &::after{
        content: '';
        display: block;
        position: absolute;
        z-index: -1;
        width: 75%;
        top: 0;
        right: 0;
        height: 100%;
        border-radius: 2.08vw;
        background: rgb(255, 255, 255);
        box-shadow: 10px 0px 30px 0px rgba(0, 0, 0, 0.05);
      }
      &-title{
        display: block;
        font-weight: 700;
        line-height: 1.5;
        font-size: 1.04vw;
      }
      &-clear{
        margin-top: 1.2vw;
      }

      &&_desktop{
        display: flex;
        height: auto;
      }
    }

    &__list{
      font-size: 1.04vw;
      padding-left: 1.6vw;
      padding-bottom: .8vw;
      margin-top: 3.55vw;

      li{
        margin-bottom: 1.8vw;
      }
    }

    &__slider{

      &-wrap{

        .swiper-slide{
          width: 16.08vw;
          padding: 0 .93vw;
          border-right-width: 0;

          &:not(:last-child)::after{
            content: '';
            display: block;
            position: absolute;
            width: 1px;
            top: 1.7vw;
            bottom: 1.7vw;
            right: 0;
            background: rgb(204, 221, 226);
          }
          &:first-child{
            display: none;
          }

          &:hover{
            
            .compra__slide{
              background: rgb(237, 243, 245);
            }
            .compra__remove,
            .compra__adds{
              opacity: 1;
            }
          }
        }
      }
    }
    &__slide{
      padding: 3200/1920vw;
      border-radius: 1.56vw;
      background: #fff;
      transition: 0.3s background;
    }
    &__pic{
      margin-bottom: 1.1vw;
      
      img{
        border-radius: 1.04vw;
      }
    }
    &__remove{
      top: 0.52vw;
      right: 0.52vw;
      opacity: 0;
      transition: 0.3s opacity;

      .square-btn{
        width: 3600/1920vw;
      }
    }
    &__link{
      margin-bottom: 1vw;
      font-size: 1.04vw;
      transition: 0.3s color;

      &:hover{
        color: var(--color-main);
      }
    }
    &__slider{
      width: calc(100% - 15.3125vw);
    }
    &__scrollbar-1{
      margin-bottom: 3200/1920vw;
      margin-left: 14600/1920vw;
    }
    &__scrollbar-2{
      margin-top: 3200/1920vw;
      margin-left: 14600/1920vw;
    }
    &__adds{
      opacity: 0;
      transition: 0.3s opacity;
    }
  }
}
