.home{
  padding: 0 0 6vw;
  overflow: hidden;

  &__wrap{
    margin-top: 6vw;
  }
  &__aside{
    margin-bottom: 8vw;
  }
  &__user{
    display: flex;
    align-items: center;
    margin-bottom: 4vw;
    font-weight: 700;
    line-height: 1.2;
    font-size: 5vw;

    &>i{
      display: block;
      width: 14vw;
      flex: 0 0 14vw;
      margin-right: 4vw;
      border-radius: 50%;
      background: #EDF3F5;
    }
    &>div{
      flex: 1;
      width: calc(100% - 24vw);

      span{
        max-width: 100%;
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
  &__head-1{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 4vw;
  }
  &__header-1{
    display: block;
    font-weight: 700;
    font-size: 1.6em;
      
    &[role="button"]:hover > i svg{
      fill: var(--color-2);
    }

    &>i{
      display: inline-block;
      position: relative;
      top: 0.1em;
      width: .84em;
      margin-left: 0.3em;

      svg{
        fill: var(--color-main);
        transition: 0.3s fill;
      }
    }
  }
  &__back{
    margin-right: 4vw;
    display: block;
    width: 8vw;
    transition: 0.3s color;

    &:hover{
      color: var(--color-main);
    }

    svg{
      fill: currentColor;
    }
  }
  &__head-right{
    margin-top: 4vw;
    text-align: right;
    width: 100%;
  }

  &__nav{
    li{
      a{
        display: flex;
        align-items: center;
        padding: 0.4em 0.7em;
        border-radius: 2vw;
        font-weight: 600;
        transition: 0.3s background;

        &.active{
          font-weight: 800;
          background: #D1F4D8;

          .q{
            opacity: 0;
          }
          .a{
            opacity: 1;
          }
        }
        &[data-logout]{
          color: rgb(156, 165, 172);
          transition: 0.3s color;

          &>i svg{
            stroke: rgb(156, 165, 172);
            transition: 0.3s stroke;
          }

          &:hover{
            color: #000;

            &>i svg{
              stroke: var(--color-main);
            }
          }
        }
 
        @media (hover:hover) {
          &:hover{
            font-weight: 800;
            background: #D1F4D8;
            letter-spacing: 0.01em;
          }
        }

        &>i{
          display: block;
          position: relative;
          width: 7vw;
          flex: 0 0 7vw;
          margin-right: 4vw;

          .q{
            position: relative;
            opacity: 1;
          }
          .a{
            opacity: 0;
            position: absolute;
            top: 0;
            left: 0;
          }
          .a, .q{
            transition: 0.3s opacity;
          }
        }
      }
    }
  }

  &__grid{
    margin-bottom: 10vw;

    &-cell{
      margin-bottom: 6vw;
    }
  }
  &__item{
    --color-bg: rgb(237, 243, 245);
    display: block;
    position: relative;
    padding: 6vw;
    border-radius: 6vw;
    background: var(--color-bg);

    &[data-bg="2"]{
      --color-bg: rgb(209, 244, 216);
    }

    &-head{
      display: flex;
      align-items: center;

      &>i{
        display: block;
        width: 12vw;
        flex: 0 0 12vw;
        margin-right: 4vw;
      }
    }
    &-title{
      display: block;
      flex: 1;
      font-weight: 700;
      font-size: 6.5vw;
    }
    &-icon{
      display: flex;
      align-items: center;
      justify-content: center;
      padding-bottom: 0.1em;
      margin-left: 1em;
      width: 12vw;
      aspect-ratio: 1;
      flex: 0 0 12vw;
      border-radius: 50%;
      background: var(--color-main);
      color: #fff;
      font-weight: 600;
      font-size: 6.5vw;
      box-shadow: 8px 8px 16px 0px rgba(0, 151, 50, 0.24);
      border: 0;
      padding: 0;
      transition: 0.3s background;

      svg{
        fill: #fff;
        width: 23/30em;
      }
    }
    &-txt{
      display: block;
      margin-top: 4vw;
      font-size: 0.8em;
      color: rgb(139, 139, 139);
      line-height: 22/16;
    }
    &-btns{
      margin-top: 6vw;
      text-align: center;
    }
  }
  &__delivs{
    margin-top: 6vw;

    &-item{
      margin-bottom: 4vw;
      display: flex;
      align-items: center;
      background: #fff;
      border-radius: 3vw;
      padding: 3vw;

      &:last-child{
        margin-bottom: 0;
      }

      &:hover{
        .link-next{
          background: var(--color-main);
          color: #fff;
        }
      }
    }
    &-pic{
      display: block;
      position: relative;
      overflow: hidden;
      border-radius: 2vw;
      width: 12vw;
      flex: 0 0 12vw;
      margin-right: 3vw;

      img{
        aspect-ratio: 1;
      }
    }
    &-btns{
      width: 7.8vw;
      margin-left: 3vw;
    }
    &-desc{
      width: calc(100% - 15vw - 10.8vw);
    }
    &-title{
      display: block;
      font-weight: 700;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    &-txt{
      display: block;
      font-size: 0.8em;
      font-weight: 300;
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  &__slider{
    margin-top: 8vw;

    .swiper-slide{
      padding-right: 4vw;
      width: 70vw;
      // max-width: 212px;
    }

    &-slide{
      position: relative;
      border-radius: 4vw;
      overflow: hidden;

      .square-btn{
        position: absolute;
        z-index: 10;
        top: 4vw;
        right: 4vw;
      }
    }
    &-nav{
      margin-top: 15px;
      max-width: none;
    }
    &-link{
      display: block;
      position: relative;

      img{
        aspect-ratio: 212/140;
      }

    }
    &-on-isset{
      display: block;
      position: absolute;
      z-index: 3;
      bottom: 4vw;
      left: 50%;
      transform: translateX(-50%);
      padding: .4em 1.4em .45em;
      border-radius: 2vw;
      background: #fff;
      font-size: 0.8em;
      font-weight: 700;
      white-space: nowrap;

      &+img{
        opacity: 0.5;
      }
    }
  }

  &__address{
    margin-top: 6vw;

    li{
      margin-bottom: 4vw;
      display: flex;
      align-items: center;

      &:last-child{
        margin-bottom: 0;
      }

      &>i{
        display: block;
        width: 10vw;
        flex: 0 0 10vw;
        margin-right: 1vw;
      }
      &>div{
        width: calc(100% - 11vw);
      }
    }
    &-title{
      display: block;
      font-weight: 700;
    }
    &-txt{
      display: block;
      font-size: 0.8em;
      font-weight: 300;
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  &__empty-item{
    padding: 15vw 0;
    text-align: center;
    color: rgb(166, 196, 206);
    font-weight: 600;
  }
  &__box-empty{
    margin: 5vw 0;
    padding: 8vw 6vw;
    border-radius: 6vw;
    background: #D2EEF4;
    text-align: center;
    line-height: 1.4;

    p{
      margin-bottom: 6vw;
    }
  }

  &__cat-types{
    margin: 0 -4.44vw 4vw;
    padding-left: 4.44vw;

    .swiper{
      overflow: visible;
    }
    .swiper-slide{
      width: max-content;
      padding-right: 2vw;
    }
  }
}


@media (min-width: @w-lg) {
  .home{
    padding-bottom: 4vw;
    overflow: visible;

    &__head{
      display: flex;
      align-items: center;
      margin-bottom: 2.08vw;
      margin-top: -0.6vw;

      .catalog__sorts{
        margin-bottom: 0;
      }

      &-right{
        margin-top: 0;
        width: auto;
        margin-left: 2vw;
      }
    }
    &__head-1{
      margin-bottom: 2.08vw;
    }
    &__header-1{
      margin-right: auto;
      margin-bottom: 0;
      font-size: 3600/1920vw;
    }
    &__wrap{
      margin-top: 1.8vw;
      display: flex;
      align-items: flex-start;
    }
    &__aside{
      width: 33000/1920vw;
      margin-right: 1.8vw;
      margin-bottom: 0;
      display: flex;
      flex-direction: column;
      height: 78vh;
      margin-left: -0.73vw;
      position: sticky;
      top: 7vw;
      left: 0;
      z-index: 50;
    }
    &__container{
      width: 133000/1920vw;

      .catalog__goods{
        width: 100%;
      }
      .catalog__cards{
        width: 62.8125vw;
      }
    }
    &__user{
      margin-bottom: 4600/1920vw;
      font-size: 1.45vw;

      &>i{
        width: 8000/1920vw;
        flex-basis: 8000/1920vw;
        margin-right: 1.04vw;
      }
    }
    &__nav{

      &&_v2{
        margin-top: auto;
        margin-bottom: 0;

        li{
          &:last-child{
            margin-bottom: 0;
          }
        }
      }

      li{
        margin-bottom: 0.6vw;

        a{
          // width: max-content;
          width: 30000/1920vw;
          padding: 0.4em 1em 0.4em 0.8em;
          font-size: 1.04vw;
          border-radius: .73vw;

          &>i{
            width: 3600/1920vw;
            flex-basis: 3600/1920vw;
            margin-right: 0.73vw;
          }
        }
      }
    }

    &__grid{
      display: flex;
      flex-wrap: wrap;
      gap: 1.3vw 1.45vw;
      margin-bottom: 2.2vw;

      &-cell{
        margin: 0;

        &&_v1{
          width: 48400/1920vw;
        }
        &&_v2{
          width: 42.5vw;
        }
      }
    }
    &__item{
      // height: 100%;
      min-height: 35000/1920vw;
      padding: 1.75vw 2.25vw 1.75vw 2.25vw;
      border-radius: 4400/1920vw;

      &-head{

        &>i{
          width: 4700/1920vw;
          flex-basis: 4700/1920vw;
          margin-right: 0.83vw;
        }
      }
      &-txt{
        margin-top: 0.73vw;
        font-size: .83vw;
      }
      &-title{
        font-size: 3200/1920vw;
      }
      &-icon{
        position: relative;
        top: .2vw;
        margin-top: -0.6vw;
        margin-right: -0.9vw;
        width: 3.12vw;
        flex-basis: 3.12vw;
        font-size: 1.56vw;

        &[type="button"]:hover{
          background: var(--color-2);
          cursor: pointer;
          outline: none;
        }
      }
      &-btns{
        margin-top: 2.1vw;

        .btn1{
          min-width: 1px;
        }
      }
    }
    &__delivs{
      margin-top: 1.5vw;
      max-height: 15.9vh;
      padding-right: 1vw;
      margin-right: -1vw;
      overflow: auto;

      &::-webkit-scrollbar-track{
        border-radius: 3px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar{
        width: 6px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar-thumb{
        border-radius: 3px;
        background-color: var(--color-main);
      }
    
      /* for Firefox */
      scrollbar-color: var(--color-main) #CFF2DD;
      scrollbar-width: thin;
      
      &-item{
        margin-bottom: .52vw;
        border-radius: .73vw;
        padding: .6vw .83vw .8vw;
      }
      &-pic{
        width: 2.2vw;
        flex-basis: 2.2vw;
        margin-right: 0.8vw;
        border-radius: 800/1920vw;
      }
      &-btns{
        width: 1.45vw;
        margin-left: .8vw;
      }
      &-desc{
        width: calc(100% - 3vw - 2.25vw);
      }
      &-title{
        font-size: 1.04vw;
        margin-bottom: 0.1vw;
      }
      &-txt{
        font-size: .63vw;
      }
    }
    
    &__address{
      margin-top: 1.4vw;
      max-height: 7.8vw;
      overflow: auto;
      padding-right: 1vw;

      &::-webkit-scrollbar-track{
        border-radius: 3px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar{
        width: 6px;
        background-color: #CFF2DD;
      }
      &::-webkit-scrollbar-thumb{
        border-radius: 3px;
        background-color: var(--color-main);
      }
    
      /* for Firefox */
      scrollbar-color: var(--color-main) #CFF2DD;
      scrollbar-width: thin;
      
      li{
        margin-bottom: 1.12vw;

        &>i{
          width: 1.77vw;
          flex-basis: 1.77vw;
          margin-right: .42vw;
        }
        &>div{
          width: calc(100% - 2.2vw);
        }
      }
      &-title{
        font-size: 1.04vw;
        margin-bottom: 0.1vw;
      }
      &-txt{
        font-size: .63vw;
      }
    }

    &__slider{
      margin-top: 1.4vw;
      position: relative;
      padding-right: 2.7vw;
      padding-bottom: 0.52vw;

      .swiper-slide{
        // width: 21200/1920vw;
        padding-right: .73vw;
        // box-sizing: content-box;
      }
      &-slide{
        border-radius: 1.04vw;

        .square-btn{
          width: 3600/1920vw;
          top: 0.52vw;
          right: 0.52vw;
        }
      }
      &-nav{
        margin-top: 0;
        position: static;

        .slider__arrow_prev{
          display: none;
        }
        .slider__arrow_next{
          position: absolute;
          right: 0;
          top: 50%;
          transform: translate(38%, -50%);
        }
      }
      &-empty{
        height: 14000/1920vw;
        font-size: 1.5vw;
      }
      &-on-isset{
        bottom: 1.04vw;
        border-radius: .52vw;
        font-size: .83vw;
      }
    }
    &__back{
      width: 1.45vw;
      margin-right: 2.08vw;
    }

    &__empty-item{
      display: flex;
      align-items: center;
      justify-content: center;
      height: 9.2vw;
      padding: 1vw 0;
      font-size: 1.04vw;
    }
    &__box-empty{
      width: 72400/1920vw;
      margin: 7.6vw auto;
      padding: 2vw 3vw 2.7vw;
      border-radius: 2.6vw;
      font-size: 1.45vw;

      p{
        margin-bottom: 1.8vw;
      }
    }
    
    &__cat-types{
      margin: 0 -5.5vw 2vw 0;
      padding: 0;

      .swiper{
        overflow: hidden;
      }
      .swiper-slide{
        padding: 0 0.52vw;
      }
    }
  }
}
