.settings{
	margin-top: 6vw;
	padding-bottom: 12vw;

	&__fieldset{
		padding: 0;
		margin: 0 0 6vw;
	}
	&__label{
		display: block;
		margin-bottom: 3vw;
		font-size: 0.8em;
		color: rgb(139, 139, 139);
		font-weight: 700;
	}
	&__input{
		display: inline-block;
		width: 100%;
		height: 12vw;
		border: 1px solid rgb(204, 221, 226);
		border-radius: 2vw;
		background: none;
		padding: .25em 1.5em .2em;
		font-family: var(--font-averta);
		font-size: 1em;
		font-weight: 400;
		outline: none;

		&::placeholder{
			color: rgb(139, 139, 139);
		}
	}
	&__box-input-icon{
		position: relative;

		.settings__input{
			position: relative;
			z-index: 1;
			padding-left: 13vw;
		}
	}
	&__icon-input{
		position: absolute;
		z-index: 0;
		top: 50%;
		left: 4vw;
		width: 8vw;
		transform: translate(0%, -50%);
	}
	&__col-1-1{
		margin-bottom: 4vw;
	}
	&__pass,
	&__remove{
		padding-bottom: 10vw;
	}
	&__remove{
		text-align: right;
	}
}


@media (min-width: @w-lg) {
	.settings{
		margin-top: 2vw;
		width: 67600/1920vw;
		padding-bottom: 4vw;

		&__fieldset{
			margin-bottom: 2.1vw;
		}
		&__label{
			margin-bottom: 0.83vw;
			font-size: 0.73vw;
		}
		&__row-1{
			display: flex;
			flex-wrap: wrap;
			gap: .52vw .73vw;
		}
		&__col-1-1{
			width: 33000/1920vw;
			margin: 0;
		}
		&__col-1-2{
			width: 100%;
		}
		&__input{
			height: 5400/1920vw;
			border-radius: .62vw;
			font-size: .83vw;
		}
		&__box-input-icon{
	
			.settings__input{
				padding-left: 3.3vw;
			}
		}
		&__icon-input{
			width: 1.56vw;
			left: 1.3vw;
		}
		&__box-submit{
			margin-top: 5000/1920vw;
		}

		&__pass{
			margin: 0;
			position: absolute;
			top: 0;
			right: 0;
			width: 48400/1920vw;

			.home__item{
				min-height: 1px;
			}
		}
		&__remove{
			margin-top: 6.5vw;
			padding-bottom: 2.2vw;
		}
	}
}
