.infoorder{
  padding-bottom: 10vw;

  &__tags{
    margin-top: 4vw;
    margin-left: auto;
    display: flex;
    justify-content: flex-end;
    gap: 3vw;
    font-size: 0.8em;

    li{
      display: flex;
      align-items: center;
      padding: .4em .8em .45em;
      border-radius: 2vw;
      background: var(--color-main);
      color: #fff;

      &>i{
        display: block;
        width: 6vw;
        margin-right: 2vw;
      }
    }
  }
  &__scrollbar{
    margin-top: 8vw;
  }
  &__slider{
    &-wrap{
      margin-right: -4.44vw;

      .swiper-slide{
        width: 80vw;
        padding-right: 4vw;
        box-sizing: content-box;
      }
    }
  }

  &__info{
    margin-top: 12vw;
  }
}


@media (min-width: @w-lg) {
	.infoorder{
    padding-bottom: 1vw;
    
    &__tags{
      margin-top: 0;
      font-size: .73vw;
      gap: 1.19vw;

      li{
        border-radius: .52vw;
        padding: 0.3em 1.4em 0.3em;

        &>i{
          width: 1.15vw;
          margin-right: .41vw;
        }
      }
    }
    &__slider{
      &-wrap{
        margin-right: -5.7vw;

        .swiper-slide{
          width: 59000/1920vw;
          padding-right: 3400/1920vw;
        }
        .cart__title{
          white-space: normal;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;  
          overflow: hidden;
        }
        .cart__pic{
          width: 11.4vw;
          margin-right: 1.9vw;
        }
        .cart__desc{
          width: calc(100% - 13.3vw);
          margin: 0;
          padding-top: 0.32vw;
          display: flex;
          flex-direction: column;
          align-self: stretch;
        }
        .cart__title{
          line-height: 0.95;
        }
        .cart__art{
          margin-bottom: 0.9vw;
        }
        .cart__sizes{
          margin-top: 0.9vw;
        }
        .cart__price-item{
          margin-top: auto;
        }
      }
    }
    &__scrollbar{
      margin-top: 1.56vw;
    }
    &__info{
      margin-top: 2.6vw;
    }
  }
}
