.uaddrs{
  margin-top: 6vw;

  &__items{
    padding-bottom: 16vw;
  }
  &__item{
    margin-bottom: 6vw;
    position: relative;
    padding: 6vw;
    border-radius: 6vw;
    background: rgb(237, 243, 245);
  }
  &__head{
    margin-bottom: 6vw;
  }
  &__btns{
    width: max-content;
    margin-left: auto;
    margin-top: 4vw;
    display: flex;
    gap: 4vw;
  }
  &__title{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 1.2em;
    line-height: 1.1;

    &>i{
      display: block;
      width: 10vw;
      flex: 0 0 10vw;
      margin-right: 4vw;
    }
  }
  &__header{
    display: block;
  }
  &__mess{
    margin-top: 3vw;
    color: rgb(139, 139, 139);
    font-size: 0.8em;
    line-height: 20/14;
  }

  &__bottom{
    margin-top: 12vw;
    text-align: center;
  }
}


@media (min-width: @w-lg) {
	.uaddrs{
    margin-top: 2.1vw;

    &__items{
      width: 73000/1920vw;
      padding-bottom: 3vw;
    }
    &__item{
      margin-bottom: 0.83vw;
      border-radius: 4400/1920vw;
      padding: 1.4vw 1.4vw 1.8vw 2.2vw;
    }
    &__btns{
      gap: .52vw;
      margin: 0;
      position: absolute;
      z-index: 5;
      top: 1.4vw;
      right: 1.4vw;

      .square-btn{
        width: 3600/1920vw;
      }
    }
    &__head{
      margin-right: 7vw;
      margin-bottom: 1.2vw;
    }
    &__title{
      flex: 1;
      font-size: 1.45vw;
      padding-top: 0.4vw;

      &>i{
        width: 4700/1920vw;
        flex-basis: 4700/1920vw;
        margin-right: .73vw;
      }
    }
    &__header{
      font-size: 1.04vw;
    }
    &__mess{
      margin-right: 10vw;
      margin-top: 0.5vw;
      font-size: 0.73vw;
    }
    &__bottom{
      margin-top: 2.1vw;
    }
  }
}
